.home
{
  position: relative;
  margin-top: 3.125rem; /* 50/16 */
  padding-top: 5%;
  padding-right: 6.25%;
  padding-bottom: 21%;
  padding-left: 6.25%;
}

.home__wrap
{
  flex-direction: row;
  width: 100%;
}

.home__heading
{
  lost-column: 1/2 0 0;

  flex-direction: column;
}

.home__image
{
  lost-column: 1/2 0 0;

  justify-content: flex-end;
  align-items: flex-start;
}

.home__image img
{
  width: 81.25%;
}

@media (--m)
{
  .home
  {
    margin-top: 6.375rem; /* 102/16 */
    transition: all .2s;
  }
}
